import * as React from "react"
import Layout from "../../components/layout"
import { Container, Box, Heading, Kicker, Text } from "../../components/ui"
import { graphql } from "gatsby"
import Feature from "../../components/feature"

export default function Insights(props) {
  const { title, kicker, heading, text, content } =
    props.data.contentfulHomepageFeatureList

  return (
    <Layout>
      <Box paddingY={2}>
        <Container width="narrow">
          <Heading as="h1">{title}</Heading>
        </Container>
      </Box>
      <Box background="muted" radius="large" style={{ color: "white" }}>
        <Box center paddingY={3}>
          <Heading>
            {kicker && <Kicker>{kicker}</Kicker>}
            {heading}
          </Heading>
          {text && <Text>{text}</Text>}
        </Box>
        {content.map((feature, i) => (
          <Feature key={feature.id} {...feature} flip={true} />
        ))}
      </Box>
    </Layout>
  )
}

export const query = graphql`
  query {
    contentfulHomepageFeatureList(articleType: { eq: "Case-study" }) {
      articleType
      heading
      text
      kicker
      content {
        slug
        articleType
        heading
        id
        kicker
        heading
        text
        links {
          id
          href
          text
        }
        image {
          id
          gatsbyImageData
          alt
        }
      }
    }
  }
`
